#root {
  font-family: "PT Sans", sans-serif;
}
.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 0.5rem;
  font-weight: 500;
  line-height: 1.2;
  color: #005ea3;
}
.default-icon {
  font-weight: 500;
  line-height: 1.2;
  color: #005ea3;
  font-size: 1.5rem;
}
.ems-btn {
  padding: 0.375rem 1.25rem;
  line-height: 1.125rem;
  background: none;
  font-size: 1.125rem;
  font-weight: 500;
  letter-spacing: 0.1rem;
  margin: 0 15px;
}
.default {
  border: 1px solid #005ea3 !important;
  border-radius: 1rem;
  border-top-right-radius: 1rem !important;
  border-bottom-right-radius: 1rem !important;
  border-top-left-radius: 1rem !important;
  border-bottom-left-radius: 1rem !important;
  color: #005ea3;
}
.main-container {
  min-height: calc(100vh - 4rem);
  z-index: 0;
  background-color: #f1f1f1;
  position: absolute;
  top: 4rem;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-x: hidden;
  overflow-y: scroll;
}
.content {
  background-color: #fff;
  position: absolute;
  top: 1.5rem;
  left: 1.5rem;
  right: 1.5rem;
  margin-bottom: 1.5rem;
  min-height: calc(100vh - 7rem);
  box-shadow: 1px 1px 3px #d3d3d3;
  box-sizing: border-box;
}
.page-header {
  margin: 0 1rem;
  border-bottom: 1px solid #f7f7f7;
  display: inline-block;
  width: calc(100% - 2rem);
}
.nav-left {
  float: left;
}
.content nav.page-header > ul {
  margin: 0;
  padding: 0 1rem;
}
ul.nav-left,
ul.nav-right {
  list-style: none;
  display: flex;
  align-items: center;
}
nav.page-header ul.nav-left li {
  margin-right: 1rem;
  font-size: 110%;
  letter-spacing: 0.1rem;
  line-height: 2rem;
  color: #005ea3;
}
nav.page-header ul > li {
  height: 2rem;
  margin: 1.5rem 0;
}
input[type="radio"] {
  height: 0;
  width: 0;
  opacity: 0;
}
