.toolbar {
  width: 100%;
  position: fixed;
  height: 4rem;
  top: 0;
  left: 0;
  background: white;
  box-shadow: 0 2px 2px -2px gray;
}
.toolbar_nav {
  display: flex;
  align-items: center;
  height: 100%;
}
.logo_letters {
  font-size: 28px;
  line-height: 70px;
  letter-spacing: -1px;
  font-kerning: none;
  margin: 0px 10px;
  /* padding: 0px; */
  text-transform: uppercase;
  background: linear-gradient(45deg, rgb(13, 97, 188) 15%, rgb(56, 152, 234));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: rgb(11, 35, 73);
  /* display: inline-block; */
  /* overflow-wrap: break-word; */
}
