.calculator-container {
  display: flex;
  justify-content: center;
}
.calculator-form {
  width: 600px;
  margin: 0 1rem 0;
  /* margin: 0; */
  box-shadow: 1px 1px 3px #d3d3d3;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 30px 30px;
}
.graph-container {
  display: flex;
  flex-direction: column;
}
.graph-form {
  width: 700px;
  height: 350px;
  margin: 0 1rem 0;
  /* margin: 0; */
  box-shadow: 1px 1px 3px #d3d3d3;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 30px 30px;
}
.calculator-line-elements {
  display: flex;
  justify-content: flex-start;
  width: 100%;
}
.calculator-item {
  width: 100%;
  /* height: 800px; */
  margin-bottom: 2.5rem;
}
.calculator-submit {
  background: #107dcc;
  color: white;
  font-size: 150%;
  border: 0;
  border-radius: 0.5rem;

  padding: 1rem 1rem;
  line-height: 1.125rem;
  font-weight: 400;
  align-self: center;
  /* letter-spacing: 0.1rem; */
}
.calculator-textarea {
  width: 100%;
  height: 8rem;
  background: #f1f1f1;
}
.calculator-title {
  margin-bottom: 1.5rem;
}
.react-datepicker__input-container input {
  width: 6rem !important;
}
.graph-form-bar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  height: 50px;
}
.graph-data-container {
  display: flex;
}
.graph-image-container {
  height: 250px;
  width: 75%;
  margin-right: 5%;
}
.graph-stats-container {
  width: 20%;
}
.graph-image {
  width: 100%;
  height: 100%;
}
.dash {
  margin: 0 20px;
}
.no-left-margin {
  margin-left: 0 !important;
}
.modal-element {
  margin: 1rem auto;
}
.graph-button {
  margin-top: 5rem;
}
